.Indicator {
    .letter {
        display: inline-block;
        background-color: black;
        box-shadow: inset 0 0 1px rgb(59, 59, 59);
        position: relative;

        .letter-symbol {
            display: block;
            position: relative;
            font-family: "digital7";
            transform: skewX(-9deg);
        }
        .letter-dot {
            display: block;
            position: absolute;

            font-family: "digital7";
            transform: skewX(-9deg);
        }
    }
}
