.EmulationComponentWrapper {
    width: 100%;
    height: 1000px;
    background: black;

    .EmulationComponent {
        width: 140px;
        height: 100px;
        background: rgb(108, 108, 108);
        position: relative;
        margin: 30px 0 0 50px;
        transform: translate(500px, 200px) scale(5.05);

        .temp-indicator {
            position: absolute;
            top: 10px;
            left: 5px;
            letter-spacing: 30px;
        }
        .timer-indicator {
            position: absolute;
            top: 50px;
            left: 5px;
            letter-spacing: 30px;
        }
    }
}
